export interface Folder {
    id: number;
    name: string;
    description?: string;
    parentId?: number;
    createdBy: string;
    createdAt: Date;
    updatedAt: Date;
    sortOrder?: number; // 폴더 순서 관리
}

export interface FolderTree {
    id: number;
    name: string;
    description?: string;
    parentId?: number;
    createdBy: string;
    createdAt: Date;
    updatedAt: Date;
    children: FolderTree[];
    testCaseCount: number;
    sortOrder?: number;
}

export interface CreateFolderRequest {
    name: string;
    description?: string;
    parentId?: number;
    createdBy: string;
}

export interface UpdateFolderRequest {
    name?: string;
    description?: string;
    parentId?: number;
    updatedBy: string;
}

export interface MoveFolderRequest {
    targetParentId?: number;
    updatedBy: string;
}

export interface MoveTestCaseRequest {
    targetFolderId: number;
    updatedBy: string;
}

// 드래그 앤 드롭 관련 타입 정의
export interface ReorderFolderRequest {
    targetFolderId: number;
    position: 'before' | 'after';
    updatedBy: string;
}

export interface DragDropResult {
    success: boolean;
    message?: string;
    folder?: Folder;
}

// 드롭 타입 구분
export enum DropType {
    REORDER = 'reorder',    // 순서 변경 (같은 부모 내에서)
    HIERARCHY = 'hierarchy' // 계층 변경 (부모 변경)
}

export interface DropZone {
    type: DropType;
    targetId: number;
    position?: 'before' | 'after';
} 